<template>
  <div class="agency">
    <!-- 面包屑 -->
    <breadcrumb :routeMeta="$route.meta"></breadcrumb>
    <!-- 卡片 -->
    <el-card>
      <el-row :gutter="20">
        <!-- 搜索框 -->
        <el-col :span="8">
          <el-input
            @clear="getAgList"
            v-model="queryInfo.agency_code"
            placeholder="请输入需要查找的代理码"
            clearable
          >
            <template #prepend>
              <el-select v-model="queryInfo.agency_level" placeholder="请选择等级" style="width:100px">
                <el-option label="全部" :value="null"></el-option>
                <el-option label="1级" :value="1"></el-option>
                <el-option label="2级" :value="2"></el-option>
              </el-select>
            </template>
            <template #append>
              <el-button icon="el-icon-search" @click="getAgList"></el-button>
            </template>
          </el-input>
        </el-col>
        <el-col :span="16" class="button_position">
          <el-button type="primary" @click="handleFindMyUser">查询名下用户</el-button>
        </el-col>
      </el-row>
      <el-divider></el-divider>

      <!-- 代理人数据展示的表格 -->
      <el-table :data="agList" border stripe>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="agency_name" label="代理人名"></el-table-column>
        <el-table-column prop="agency_code" label="邀请码"></el-table-column>
        <el-table-column prop="agency_level" label="等级" align="center">
          <template #default="val">
            <el-tag type="success">等级 {{ val.row.agency_level }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="create_time" label="创建时间"></el-table-column>
        <el-table-column label="操作" width="200" align="center">
          <template #default="val">
            <!-- <el-button
              @click="handleFindUser(val.row)"
              size="mini"
              type="success"
              icon="el-icon-s-tools"
              v-if="$PowerKey(powerKey.AgPowerKey.FindUser)"
              >查询用户</el-button
            >-->
            <el-button
              @click="handleFindMember(val.row)"
              size="mini"
              type="warning"
              icon="el-icon-s-tools"
              v-if="$PowerKey(powerKey.AgPowerKey.FindMember)"
            >查询用户</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <pagination
        :total="listTotal"
        :pageSizes="[5, 10, 20]"
        :queryInfo="queryInfo"
        :queryFunc="getAgList"
      ></pagination>
    </el-card>
    <!-- 主体页面 结束 -->

    <!-- 信息列表的弹窗  userDiaLog.msgInfo -->
    <el-dialog
      class="ag_dialog"
      title="用户列表"
      :visible.sync="agDiaLog.agInfo"
      @close="handleCloseDialog"
      :close-on-click-modal="false"
    >
      <el-row :gutter="20">
        <el-col :span="20">
          <el-input
            @clear="getMuList"
            v-model="mQueryInfo.search_value"
            placeholder="请输入需要查找的内容"
            clearable
          >
            <template #prepend>
              <el-select
                v-model="mQueryInfo.member"
                placeholder="用户类型"
                style="width:120px"
                @change="getMuList"
              >
                <el-option label="全部" :value="null"></el-option>
                <el-option label="非会员" :value="0"></el-option>
                <el-option label="会员" :value="1"></el-option>
              </el-select>
            </template>
            <template #append>
              <el-button icon="el-icon-search" @click="getMuList"></el-button>
            </template>
          </el-input>
        </el-col>
      </el-row>
      <div class="ag_body">
        <el-table :data="muList" border stripe>
          <el-table-column type="index" label="#"></el-table-column>
          <el-table-column prop="name" label="姓名"></el-table-column>
          <el-table-column prop="login_account" label="账号"></el-table-column>
          <el-table-column prop="mobile_phone" label="手机"></el-table-column>
          <el-table-column prop="email" label="邮箱"></el-table-column>
          <el-table-column label="性别" align="center">
            <template #default="val">
              <el-tag
                :type="commonFn.UserSex(val.row.sex).color"
              >{{commonFn.UserSex(val.row.sex).name}}</el-tag>
            </template>
          </el-table-column>
          <el-table-column label="用户类型" width="100" align="center">
            <template #default="val">
              <el-tag
                :type="commonFn.UserType(val.row.member).color"
              >{{commonFn.UserType(val.row.member).name}}</el-tag>
            </template>
          </el-table-column>
          <el-table-column v-if="postNum==1" prop="agency_name" label="代理人"></el-table-column>
          <el-table-column prop="last_login_time" label="最后登陆时间"></el-table-column>
          <!-- </div> -->
        </el-table>
      </div>
      <pagination
        v-if="postNum != null"
        :total="muTotal"
        :background="true"
        :pageSizes="[5, 10, 20]"
        :queryInfo="mQueryInfo"
        :queryFunc="getMuList"
        layout="total,sizes,prev, pager, next"
      ></pagination>
    </el-dialog>
  </div>
</template>

<script>
import {
  getAgencyListApi,
  getAgencyMemberListApi,
  getAgencyUserListApi
} from "@/api/api";

import Breadcrumb from "@/components/Breadcrumb.vue";
import Pagination from "@/components/Pagination.vue";

import { mapState } from "vuex";

import { ruleEmail, ruleMobile } from "@/rules/index.js";

export default {
  name: "agency",
  created() {
    // 按钮权限
    console.log(this.$route.path);
    this.agBtnList = this.powerList[this.$route.path];
  },
  mounted() {
    this.getAgList();
  },
  computed: {
    ...mapState("menu", ["powerList"])
  },
  data() {
    return {
      postNum: null, //普通用户为 1， 会员为 2
      agBtnList: [],
      listTotal: null,
      muTotal: null,
      // 查询信息
      queryInfo: {
        agency_code: null,
        agency_level: null,
        pageParam: {
          page: 1,
          pageSize: 5
        }
      },
      // 弹窗的打开判断
      agDiaLog: {
        agInfo: false
      },
      // 会员查询信息
      mQueryInfo: {
        agency_id: null,
        member: null,
        search_value: null,
        pageParam: {
          page: 1,
          pageSize: 5
        }
      },
      // 具体数据
      agList: [],
      muList: []
    };
  },
  methods: {
    $PowerKey(str) {
      return this.agBtnList && this.agBtnList.some(v => v.name_en == str);
    },
    async getAgList() {
      let res = await getAgencyListApi(this.queryInfo);
      console.log(res); // 返回的数据给 agList
      if (res.code != 10000) {
        return this.$message.error(res.message);
      }
      this.agList = res.value;
      (!this.listTotal || this.listTotal != res.num) &&
        (this.listTotal = res.num);
    },

    async getMuList() {
      let res = null;
      if (this.postNum == 0) {
        return;
      } else if (this.postNum == 1) {
        res = await getAgencyUserListApi(this.mQueryInfo);
      } else if (this.postNum == 2) {
        res = await getAgencyMemberListApi(this.mQueryInfo);
      }

      if (res.code != 10000) {
        return this.$message.error(res.message);
      }
      this.muList = res.value;

      (!this.muTotal || this.muTotal != res.num) && (this.muTotal = res.num);
    },

    // 关闭 dialog 弹窗
    handleCloseDialog() {
      this.muList = [];
      this.muTotal = null;
      this.postNum = null;
      Object.assign(this.mQueryInfo, this.$options.data().mQueryInfo);
    },
    // 查询会员
    handleFindMember(row) {
      // 代理id
      this.mQueryInfo.agency_id = row.id;

      this.agDiaLog.agInfo = true;
      this.postNum = 2;
      this.getMuList();
    },
    // 查询名下所有用户
    handleFindMyUser() {
      this.agDiaLog.agInfo = true;
      this.postNum = 1;
      this.getMuList();
    }
  },

  components: {
    Breadcrumb,
    Pagination
  }
};
</script>

<style lang="scss">
.ag_dialog {
  .el-dialog {
    min-width: 600px;
  }

  .ag_body {
    height: 400px;
    overflow: auto;
  }
}

.button_position {
  position: relative;

  .el-button {
    position: absolute;
    right: 10px;
  }
}
</style>
